import "./style.css";
import React from "react";
import Navbar from "../includes/Navbarwhite";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";




function men() {

  gsap.registerPlugin(ScrollTrigger);

    gsap.to(".a",{
    //   scrollTrigger:{
    //   trigger:".a",
    //   markers:true,
    //   toggleActions:"restart pause resume pause"
    // },
    x:400,
    rotation:360,
    duration:3
    });
  
  
  return (
    
    <>
    
      <Navbar />
      <div className="content">
        <section className="description panel blue">
    <div>
      <h1 >Basic Pin</h1>
      <p>You can <strong>pin</strong> an element while the ScrollTrigger is active which basically make it stick in place while the scroll position is between the <code>start</code> and <code>end</code> elements/values</p>
      <div className="scroll-down">Scroll down<div className="arrow"></div></div>
    </div>
</section>
 

<section className="comparisonSection">
  <div className="pinContainer">
    <div className="comparisonImage beforeImage">
      <img src="https://assets.codepen.io/16327/before.jpg" alt="before"/>
    </div>
    <div className="comparisonImage afterImage">
      <img src="https://assets.codepen.io/16327/after.jpg" alt="after"/>
    </div>
  </div>
</section>


<section className="panel purple ">
  <p className="bc a"
  >xzxc</p>
  Easy Peasy!
</section>
</div>
    </>
  );
}

export default men;


gsap.utils.toArray(".comparisonSection").forEach(section => {
	let tl = gsap.timeline({
			scrollTrigger: {
				trigger: section,
				start: "center center",
        // makes the height of the scrolling (while pinning) match the width, thus the speed remains constant (vertical/horizontal)
				end: () => "+=" + section.offsetWidth, 
				scrub: true,
				pin: true,
        anticipatePin: 1
			},
			defaults: {ease: "none"}
		});
	// animate the container one way...
	tl.fromTo(section.querySelector(".afterImage"), { xPercent: 100, x: 0}, {xPercent: 0})
	  // ...and the image the opposite way (at the same time)
	  .fromTo(section.querySelector(".afterImage img"), {xPercent: -100, x: 0}, {xPercent: 0}, 0);
});